.startup-projects-main {
  display: flex;
}
.startup-projects-main > * {
  margin-bottom: 30px;
}

.starup-project-image > img {
  max-width: 100%;
  height: auto;
}

.project-card-head {
  color: rgb(36, 41, 46);
  font-size: 25px;
  font-weight: 700;
  letter-spacing: -0.5px;
  margin: 0px;
}

.startup-project-text {
  display: inline-flex;
  width: max-content;
  flex-wrap: wrap;
  gap: 20px;
}

.project-card-description {
  color: rgb(88, 96, 105);
}
#projects .subTitle {
  color: #868e96;
}

.photo-div {
  padding: 10px 20px;
  border: 1px solid #868e9694;
  background-color: #868e961e;
  border-radius: 10px;
  cursor: pointer;
  display: flex;
  width: 50%;
  max-width: 300px;
  flex-direction: column;
}
.photo-div > img {
  max-width: 250px;
  padding: 10px 10px;
  margin: 10px auto;
  border-radius: 20px;
}

.proj-title {
  text-align: center;
  font-size: 20px;
  line-height: 1rem;
}
.proj-desc {
  font-size: 16px;
  line-height: 1.4rem;
  font-stretch: condensed;
  padding: 0 10px;
  text-align: center;
}

@media (max-width: 768px) {
  .proj-desc {
    font-size: 15px;
  }
  h1.projects-header {
    display: flex;
    place-content: center;
  }
  .starup-project-image {
    display: none;
  }
  .project-subtitle {
    font-size: 16px;
    text-align: center;
  }
  .photo-div > img {
    max-width: 100%;
    margin: 0 auto;
    padding: 10px 10px;
    border-radius: 20px;
  }
}

@media (max-width: 550px) {
  .startup-project-text {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }

  .photo-div {
    display: flex;
    width: auto;
  }
  .photo-div > img {
    width: 60%;
  }
  .proj-desc {
    font-size: 14px;
    padding: 20px 10px;
  }
}
