.experience-card {
    position: relative;
    background-color: #868e961e;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 10px 30px -15px;
    border-radius: 10px;
    border: 1px solid rgba(211, 211, 211, 0.397);
}

.experience-card-dark {
    position: relative;
    background-color: #868e961e;

    box-shadow: rgba(0, 0, 0, 0.2) 0px 10px 30px -15px;
    border-radius: 10px;
    border: 1px solid rgba(211, 211, 211, 0.301);
}

.experience-card:hover {
    box-shadow: rgba(0, 0, 0, 0.2) 0px 20px 30px -10px;
}

.experience-banner {
    background-position: center;
    background-repeat: no-repeat;
    background-size: 150%;
    background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(116, 116, 116, 0.253));
    border-radius: 10px 10px 0px 0px;
    -webkit-filter: blur(0px);
    -moz-filter: blur(0px);
    -o-filter: blur(0px);
    -ms-filter: blur(0px);
    filter: blur(0px);
    height: 11rem;
    display: flex;
    align-items: flex-end;
    justify-content: center;
}

.experience-blurred_div {
    position: absolute;
    background: linear-gradient(rgba(14, 14, 14, 0.4), rgba(116, 116, 116, 0.253));
    -webkit-filter: blur(5px);
    -moz-filter: blur(5px);
    -o-filter: blur(5px);
    -ms-filter: blur(5px);
    filter: blur(5px);
    height: 11rem;
    top: 0;
    left: 0;
    width: 100%;
    border-radius: 10px 10px 0px 0px;
}

.experience-div-company {
    position: absolute;
    background: transparent;
    height: 9rem;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.experience-text-company {
    text-align: center;
    padding: 1.5rem;
    margin: 0;
    color: #fff;
    font-size: 25px;
    text-align: center;
    font-weight: 500;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.experience-roundedimg {
    position: absolute;
    object-fit: cover;
    left: 0;
    right: 0;
    top: 7rem;
    margin-left: auto;
    margin-right: auto;
    width: 8rem;
    height: 8rem;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.3);
    border-radius: 50%;
}

.experience-text-role {
    text-align: center;
    color: black;
    font-weight: 500;
    font-size: 22px;
    margin: 50px 0 0 0;
    padding-top: 2.9rem;
    line-height: normal;
}
.dark-mode-text {
    color:white !important;
}

.experience-text-date {
    text-align: center;
    color: black;
    font-size: 14px;
    margin: 0px;
    padding-top: 1rem;
    font-weight: 400;
}

.experience-text-desc {
    font-size: 14px;
    text-align: left;
}
.experience-text-details{
    font-size: 16px;
    opacity: 0.9;
    max-width: 100%;
    padding: 0 20px;
    line-height: 1.1rem;
    margin-top: 2rem;
}
.experience-text-details > ul {
    padding-inline-start: 20px;
}
/* #workExperience > div > div > div:nth-child(1) > div.experience-text-details > ul > li:nth-child(1) */
div.experience-text-details > ul > li.subTitle {
    font-size: 14px;
    line-height: 1.15rem;
    padding-bottom: 5px;
}
/* Media Query */

@media (max-width: 1380px) {
    .experience-text-role {
        padding-top: 0.5rem;
        font-size: 22px;
    }
    .experience-text-company {
        font-size: 22px;
    }
    .experience-text-date {
        font-size: 13px;
    }
    .experience-roundedimg {
        width: 6.5rem;
        height: 6.5rem;
    }
}

@media (max-width: 768px) {
    #experience .experience-cards-div {
        grid-template-rows:unset
    }
    .experience-text-role {
        font-size: 22px;
    }
    .experience-text-company {
        font-size: 22px;
    }
    .experience-text-date {
        font-size: 18px;
    }
    .experience-text-desc {
        font-size: 16px;
    }
}